* {
  margin: 0;
  padding: 0;
}

.easy-editor {
  display: flex;
  flex-direction: column;
  height: 100%;
  background: white;
  box-sizing: border-box;
}
.easy-editor-body {
  display: flex;
  overflow: auto;
  flex: 1 1;
  padding: 0;
  word-wrap: break-word;
  width: 100%;
}
.easy-editor-body .ProseMirror {
  width: 100%;
}
.easy-editor-body .ProseMirror:focus {
  outline: none;
}